import React, { useState } from "react";
import Card from "./components/Card";
import { useArray } from "./components/helpers/hooks/useArray";

function App() {
  const [todo, setTodo] = useState("");
  // const [todos,setTodos] = useState([ ])
  const {list,addItem,removeItem } = useArray([])

  const todoHandler = (event) => {
    setTodo(event.target.value);
    console.log(event.target.value);
  };

  const submitHandler = (event) => {
    event.preventDefault()
    // setTodos(prevSTate  => 
    //  [...prevSTate,{text:todo,id:Math.random().toString()} ])
    addItem({text:todo,id:Math.random().toString()})

    setTodo("")

  }

  const deleteHandler = (todoId) => {
    return () => removeItem(todoId)
  //   return () => {
  //     const updatedTodos = todos.filter((todo) => todo.id === todoId); //barabar bolboso chygar kalganyn chygarba
  //     setTodos(updatedTodos)

  //   }
  }


    
  

  return (
    <Card>
      <form onSubmit={submitHandler}>
        <input value={todo} onChange={todoHandler} type="text" />
        <button >Add Change</button>
      </form>
      <div>
        <ul>
          {list.map((item) => {
            return <li key = {item.id}>{item.text}<span onClick= {deleteHandler(item.id)} style={{color:"red"}}>X</span></li>
          })}

        </ul>
      </div>
    </Card>
  );
}

export default App;
