import { useState } from "react"

export const useArray = (initList) => {  //pustoy massiv kelet
 const [list,setList] = useState(initList)

 return {
    list,
    addItem:(newItem) => {
        setList(prevState => [...prevState,newItem])
    },
    removeItem: (itemId) => {
            const updatedItems= list.filter((item) => item.id !== itemId); //barabar bolboso chygar kalganyn chygarba
            setList(updatedItems)
      
    }
 }
}